import React, { Component } from 'react'
import Container from 'react-bootstrap/Container';
import '../App.css'
import '../kartta/data/avain'

class Laatikko extends Component {
  render() {
    //Jos korrelaatiota ei ole laskettu tämä renderöidään, muuten else
    if(this.props.korrelaatio.teksti === "")
    return (
      <div className="laatikkoDiv">
      <Container className="Laatikko">
        <p className="laatikkoP">
          <b>Haettu hakusana:</b>  {this.props.sana                  === "" ? "Ei hakua"                         :this.props.sana} <br/>
          <b>Kunta:</b>            {this.props.koronaLaatikko.nimi   === "" ? "Klikkaa kuntaa nähdäksesi tiedot" :this.props.koronaLaatikko.nimi} <br/>
          <b>Trends-arvo:</b>      {this.props.koronaLaatikko.trends === "" ? "Tee haku nähdäksesi trends-arvot" :this.props.koronaLaatikko.trends} <br/>
          <b>Koronatartuntoja:</b> {this.props.koronaLaatikko.korona === "" ? "Klikkaa kuntaa nähdäksesi tiedot" :this.props.koronaLaatikko.korona} <br/>
        </p>
      </Container>
      </div>
    ); else
    console.log(this.props.korrelaatio.uskottavuus)
    return (
      <div className="laatikkoDiv">
      <Container className="Laatikko">
        <p className="laatikkoP">
        <b>Haettu hakusana:</b>  {this.props.sana                  === "" ? "Ei hakua"                         :this.props.sana} <br/>
        <b>Kunta:</b>            {this.props.koronaLaatikko.nimi   === "" ? "Klikkaa kuntaa nähdäksesi tiedot" :this.props.koronaLaatikko.nimi} <br/>
        <b>Trends-arvo:</b>      {this.props.koronaLaatikko.trends === "" ? "Tee haku nähdäksesi trends-arvot" :this.props.koronaLaatikko.trends} <br/>
        <b>Koronatartuntoja:</b> {this.props.koronaLaatikko.korona === "" ? "Klikkaa kuntaa nähdäksesi tiedot" :this.props.koronaLaatikko.korona} <br/>
        </p>
        <p>
        <b>Pearsonin korrelaatiokerroin:</b> {("" + this.props.korrelaatio.data).slice(0,6)} <br/>
        <b>Korrelaatio:</b>                  {this.props.korrelaatio.teksti} <br/>
        Datassa oli {this.props.korrelaatio.uskottavuus} merkitsevää datapistettä, joten {this.props.korrelaatio.uskottavuus < 10 ? "se ei todennäköisesti ole merkitsevä tulos" : this.props.korrelaatio.uskottavuus < 25 ? "se saattaa olla merkitsevä tulos " : this.props.korrelaatio.uskottavuus < 50 ? "se on mahdollisesti merkitsevä tulos": "se on todennäköisesti merkitsevä tulos"}
        </p>
        </Container>
        </div>
    ) 
  }
}

export default Laatikko