import React, {useState, Component} from 'react'
import { Map, TileLayer, GeoJSON } from 'react-leaflet'
import L from 'leaflet'
import eventBus from "./EventBus";
import ReactDOMServer from 'react-dom/server';
import Popup from "./Popup";
import Legend from "./Legend";

let kuntaRajat 		= require ('../data/kuntarajat-ok.json')
//let maakuntaRajat = require ('../data/maakunnat.json')
let haettuM 			= 0;

const position = [65.505, 25]
let f = 1

//Palauttaa tyylin geojsonia varten
function style(feature) {
	let color = f === 1 ? feature.properties.korona: 
							f === 2 ? feature.properties.haku:
							feature.properties.korona * (feature.properties.haku / 100)


	return {
		weight:        1,
		opacity:       5,
		color: '#1e1f1f',
		dashArray:    '',
		fillOpacity: 0.7,
		fillColor: getColor(color)
	};
}

//Palauttaa tietyn värin d:n arvon perusteella
function getColor(d) {
	return  d > 1000 ?  '#800026':
					d > 300  ?  '#BD0026':
					d > 150  ?  '#E31A1C':
					d > 75   ?  '#FC4E2A':
					d > 25   ?  '#FD8D3C':
					d > 10   ?  '#FEB24C':
					d > 4    ?  '#FED976':
											'#dcf1f2';
}

//Poistaa highlightin
function resetHighlight(e) {
	var layer = e.target;
	layer.setStyle({
		weight:        1,
		opacity:       5,
		color: '#1e1f1f',
		dashArray:    '',
		fillOpacity: 0.7,
	});
}



//Korostaa tietyn layerin
function highlightFeature(e) {
	var layer = e.target;
	layer.setStyle({
		weight:         4,
		color:     '#666',
		dashArray:     '',
		fillOpacity:  0.7
	});
	if (!L.Browser.ie && !L.Browser.opera && !L.Browser.edge) {
		layer.bringToFront();
	}
}
 
class Kartta extends Component {
  constructor(props){
    super(props)
    this.state = {
      tila: props
		}
		this.onEachFeature = this.onEachFeature.bind(this);
		this.clickFeature = this.clickFeature.bind(this);
	}
	
	onEachFeature (feature, layer){
		layer.on({
			mouseover: highlightFeature,
			mouseout:  resetHighlight,
			click:     this.clickFeature
		});
		const popupContent = ReactDOMServer.renderToString(
			<Popup feature={feature} />
		);
		layer.bindPopup(popupContent);
		
	}
	
	
	/*
	componentDidUpdate(){
		f = this.props.filtteri === f ? f : this.props.filtteri
	}
	*/


	clickFeature(e) {
		//Ottaa tietyn layerin käsittelyyn
		var layer = e.target
		console.log (layer.feature.properties)
	
		var data =  { 
			id: 		 layer.feature.properties.code,
			density: haettuM !== 0 ? layer.feature.properties.haku : "Tee haku nähdäksesi trends-arvot",
			korona:  layer.feature.properties.korona,
			name:    layer.feature.properties.name
		}	  
		//Lähettää datan eventbusin avulla (laatikolle)
		this.props.handleChange(layer.feature.properties.code)
		eventBus.dispatch("kuntadata", data);
	}	

  render(){
		
		for (const feature of kuntaRajat.features){
			let id = feature.properties.code
			this.props.koronaData[id]["data"] === undefined ? feature.properties.korona = 0 : feature.properties.korona = this.props.koronaData[id]["data"]
      
  
      haettuM = 0;
      if(this.props.hakuData[id]["data"] !== undefined){
				haettuM = 1;
				feature.properties.haku = this.props.hakuData[id]["data"]
      }	
		}

    return (
      <Map center={position} zoom={5}>
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution="&copy; <a href=&quot;http://osm.org/copyright&quot;>OpenStreetMap</a> contributors | Korona-data &copy; <a href=&quot;https://thl.fi/fi/tilastot-ja-data/aineistot-ja-palvelut/avoin-data/varmistetut-koronatapaukset-suomessa-covid-19-&quot;>THL</a>"
        />
        <Legend />
        <GeoJSON 
          data          = {kuntaRajat}				
          style         = {style}
          onEachFeature = {this.onEachFeature}
        />
				
      </Map>
    )
  }
}

export default Kartta
